.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.mp-image {
	border-radius: 93px;
}

.member {
	background: white;
	margin-bottom: 2em;
}

.member a {
	color: black;
	text-decoration: none;
}

.member a:hover {
	-webkit-box-shadow: 3px 3px 8px hsl(0, 0%, 80%);
	        box-shadow: 3px 3px 8px hsl(0, 0%, 80%);
}

.member-info {
	padding: 1.4em;
}

.member-info h2 {
	margin-top: 0;
	margin-bottom: .5em;
	font-weight: bold;
}

.members-of-parliament {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.member {
	-ms-flex: 1 1;
	    flex: 1 1;
	max-width: 200px;
	padding-top:50px;
	padding-left: 50px;
	padding-right: 50px;
}

.member-constituency {
	color: darkRed;
}



